* {
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: inherit;
}

body {
  font-family: "'Public Sans', sans-serif";
  font-weight:400;
  font-size:1rem;
  line-height:1.6;
  background:#efefef;
  color:#333;
}

html {
  scroll-behavior: smooth;
}

ul{
  list-style: none;
}

img{
  width:100%;
  display:block;
  max-width:100%;
  filter: invert(0) brightness(1);
}

button {
  border:none;
  outline:0;
  background:transparent;
}

a {
  color:unset;
  text-decoration: none;
}

.block-wrap{
  border-radius: 6px;
  padding: 20px;
  background:#fff;
  box-shadow: 0 0 0.25rem rgba(75, 70, 92, 0.1);
}

.dark .block-wrap{
  background:rgb(3, 7, 18);
  box-shadow: 0 0 0.25rem rgba(165,163,174,0.3);
}


.recharts-legend-item {
  align-items: center !important;
  margin-right: 8px;
  margin-left: 8px;
}

.recharts-surface {
  border-radius: 2px;
}

/* recharts related tooltip customization */
.recharts-default-tooltip, .custom-recharts-tooltip{
  padding:4px 8px !important;
  box-shadow:0 0.125rem 0.25rem rgba(165, 163, 174, 0.3);
  border:1px solid rgba(0, 0, 0, 0.05) !important;
  background-color: #fff !important;
  border-radius:6px;
}

.recharts-tooltip-item-list *, .recharts-tooltip-item-list li {

      font-size: 13px;
      font-family: inherit !important;
      opacity: 0.9;
      color: #333;
      text-transform: capitalize;
    
  }

  .recharts-tooltip-label {
    color: #333;
    font-size: 14px;
    font-weight: 600;
  }

  .recharts-default-legend {
    margin-top: 16px !important;
  }

  .recharts-legend-item {
      display: inline-flex !important;
      align-items: center!important;
  }

  .recharts-legend-item-text {
    text-transform: capitalize;
    color: #a5a3ae !important;
    padding-left: 4px;
    font-size: 14px;
    margin-top:5px;
  }

  /* If box has scroll overflow */
  .has-scroll > .scr-bar::-webkit-scrollbar {
    height: 6px;
    width: 6px;
  }

  .has-scroll > .scr-bar::-webkit-scrollbar-track {
    background-color: transparent;
  }

  .has-scroll > .scr-bar::-webkit-scrollbar-thumb {
    border-radius: 100vh !important;
    background-color: #e9e9e9;
    outline: 1px solid rgba(0, 0, 0, 0.02);
    outline-offset: -1px;
  }